#drum-machine {
  border: gray 2px solid;
  display: grid;
  grid-template-columns: 2fr 1fr;
  width: 800px;
  padding: 2%;
  margin: 10% auto 0 auto;
  -webkit-box-shadow: 3px 4px 5px gray;
          box-shadow: 3px 4px 5px gray; }

.drum-pad-bank {
  padding: 3%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }
  .drum-pad-bank .drum-pad {
    border-radius: 10px;
    width: 90%;
    margin-bottom: 10px;
    background-color: grey;
    -webkit-box-shadow: 3px 4px 5px black;
            box-shadow: 3px 4px 5px black;
    text-align: center;
    line-height: 100px;
    font-weight: bold; }

.controller-container {
  border: gray 2px solid;
  text-align: center;
  display: grid;
  grid-template-rows: 35% 30% 35%; }
  .controller-container .row {
    position: relative; }
    .controller-container .row.switch-container .switch {
      position: absolute;
      bottom: 15%;
      left: 35%; }
    .controller-container .row.display-container #display {
      margin: 15% auto;
      width: 70%;
      line-height: 30px;
      background-color: rgba(128, 128, 128, 0.671);
      font-family: digitalFont; }
    .controller-container .row.volume-container .range {
      position: relative;
      top: 15%; }



